import {mapState,mapMutations} from 'vuex';
import {SET_RUBBISH_SEARCH,SET_FINAL_DATA,SET_SHIBIE_OBJ} from '../../store/mutation-types.js'
const recorderManager = uni.getRecorderManager();
export default {
	data() {
		return {
			rubbishName:'',
			hotsearch:[],
			yuyinstart:false,
			openrecordSet:true
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key'
		])
	},
	methods: {
		...mapMutations([
			SET_RUBBISH_SEARCH,
			SET_FINAL_DATA,
			SET_SHIBIE_OBJ
		]),
		getinputValue:function(e){
			this.rubbishName = e.detail.value
		},
		searchfn:function(){
			var _this = this
			if(this.rubbishName == ''){
				uni.showToast({
					title:'输入内容不能为空',
					icon:'none'
				})
				return
			}
			uni.showLoading()
			var name = encodeURI(this.rubbishName)
			uni.request({
				url:_this.requesturl + '/txapi/lajifenlei/index?key=' + _this.key + '&word=' + name,
				success:res => {
					if(res.data.newslist){
						uni.hideLoading()
						console.log(res)
						_this.SET_RUBBISH_SEARCH(res.data.newslist)
						uni.navigateTo({
							url:'./searchlist/searchlist'
						})
					}else{
						uni.showToast({
							title:'未找到相关内容',
							icon:'loading'
						})
					}
				}
			})
		},
		getres:function(e){
			var name = e.currentTarget.dataset.name;
			uni.request({
				url:this.requesturl + '/txapi/lajifenlei/index?key=' + this.key + '&word=' + name,
				success:(res)=>{
					this.SET_RUBBISH_SEARCH(res.data.newslist)
					uni.navigateTo({
						url:'./searchlist/searchlist'
					})
				}
			})
			
			
		},
		knowledgemore:function(){
			uni.navigateTo({
				url:'knowledge/knowledge'
			})
		},
		allhotsearch:function(){
			uni.navigateTo({
				url:'allhotsearch/allhotsearch'
			})
		},
		getHotsearch:function(){
			uni.showLoading({
				
			})
			var _this = this
			uni.request({
				url:_this.requesturl + '/txapi/hotlajifenlei/index?key=' + _this.key,
				success:function(res){
					uni.hideLoading()
					var hotarr = res.data.newslist.splice(0,10);
					_this.hotsearch = hotarr
				}
			})
		},
		imagesort:function(){
			console.log('图像识别分类')
			var _this = this
			 uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album','camera'], //从相册选择
				success: res => {
				uni.showLoading({
					title:'图片识别中',
					mask:true
				})
				uni.getFileSystemManager().readFile({
						filePath: res.tempFilePaths[0], //选择图片返回的相对路径
						encoding: 'base64', //编码格式
						success: res => { //成功的回调
							console.log('data:image/png;base64,' + res.data)
							var imgurl = 'data:image/png;base64,' + res.data
							console.log(imgurl)
							uni.request({
								method:'POST',
								url:_this.requesturl + '/txapi/imglajifenlei/index',
								header: { 'Content-Type': 'application/x-www-form-urlencoded'},
								data:{
									key:_this.key,
									img:imgurl
								},
								success:function(res){
									console.log(res)
									var obj = res.data.newslist[0]
									var imgsortobj = {
										name:obj.name,
										type:obj.lajitype,
										imgurl:imgurl,
										lajitip:obj.lajitip
									}
									_this.SET_SHIBIE_OBJ(imgsortobj)
									uni.hideLoading()
									uni.navigateTo({
										url:'shibiepage/shibiepage'
									})
								}
							})
						}
					})
				}
			})
		},
		openrecordSetting:function(){
			var _this = this
			uni.authorize({
			    scope: 'scope.record',
			    success() {
						_this.openrecordSet = false
			    },
					fail(){
						uni.showModal({
							title:'需授权录音功能',
							content:'授权后可以正常使用语音识别',
							success:function(res){
								if (res.confirm) {
									console.log('用户点击确定');
									uni.openSetting({
									  success(res) {
									    console.log(res.authSetting)
									  }
									});
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						})
					}
			})
		},
		yuyinsorttip:function(){
			uni.showToast({
				title:'长按录音,松开结束',
				icon:'none'
			})
		},
		longpresssort:function(){
			console.log('开始录音');
			var _this = this
			_this.yuyinstart = true;
			recorderManager.start({
				duration:60000,
				sampleRate:16000,
				numberOfChannels:1,
				frameSize:1024
			});
		},
		touchendsort:function(){
			console.log('结束')
			this.yuyinstart = false;
			recorderManager.stop();
		},
		voicelajifenlei:function(voice){
			var _this = this
			console.log(voice)
			uni.showLoading({
				title:'语音识别中',
				mask:true
			})
			uni.uploadFile({
				url: _this.requesturl + '/txapi/voicelajifenlei/index', //语音垃圾分类接口
				filePath: voice,
				name: 'say',
				formData: {
					key: _this.key,
					format:'m4a'
				},
				success (res){
					
					var obj = JSON.parse(res.data).newslist[0]
					var voiceobj = {
						name:obj.name,
						type:obj.lajitype,
						imgurl:'',
						lajitip:obj.lajitip
					}
					_this.SET_SHIBIE_OBJ(voiceobj)
					uni.hideLoading()
					uni.navigateTo({
						url:'shibiepage/shibiepage'
					})
				}
			})
			
			
			// uni.request({
			// 	url:_this.requesturl + '/txapi/voicelajifenlei/index',
			// 	header: { 
			// 		'enctype':'application/x-www-form-urlencoded'
			// 	},
			// 	method:"POST",
			// 	data:{
			// 		key:_this.key,
			// 		say:voice,
			// 		format:'m4a'
			// 	},
			// 	success:function(res){
			// 		uni.hideLoading()
			// 		console.log(res)
			// 		var code = res.data.code
			// 		if(code == 200){
						
			// 		}else if(code == 290){
			// 			uni.showToast({
			// 				title:'录音时间过长',
			// 				icon:'none'
			// 			})
			// 		}
			// 	}
			// })
		}
	},
	onLoad(){
		this.getHotsearch()
		var _this = this
		uni.getSetting({
			success(res) {
				console.log(res.authSetting)
				if(res.authSetting['scope.record'] == true){
					_this.openrecordSet = false
				}else if(res.authSetting['scope.record'] == false){
					_this.openrecordSet = true
				}
			}
		})
		recorderManager.onStop(function (res) {
			console.log('获取录音文件')
			var voicefile = res.tempFilePath;
			_this.voicelajifenlei(voicefile)
		});
	},
	
}
